<template>
  <div style="height: 100%">
    <div class="rows">
      <el-collapse v-if="isReDataEditor">
        <el-collapse-item title="" name="1">
          <el-row :gutter="24">
            <el-col :span="3">$T[]</el-col>
            <el-col :span="3">$R[]</el-col>
            <el-col :span="3">$E[]</el-col>
            <el-col :span="3">$D[]</el-col>
            <el-col :span="3">$F[]</el-col>
            <el-col :span="3">$V[]</el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :span="9">$B[width] $D,$B[0] </el-col>
            <el-col :span="15">$N[t],,t,,b,</el-col>
          </el-row>
          <el-row :gutter="24">
            <el-col :span="7">$VA[value1,+value2] ()</el-col>
            <el-col :span="5">$VA[value1,*value2] ()</el-col>
            <el-col :span="6">$VA[value1,/value2] ()</el-col>
            <el-col :span="6">$VA[value1,-value2] ()</el-col>
          </el-row>
         
          <el-row :gutter="24">
            <el-col :span="12">$A[]","</el-col>
            <el-col :span="6">$AD[]</el-col>
            <el-col :span="6">$ADD[]</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="6">$S[dotted]</el-col>
            <el-col :span="6">$S[solid]</el-col>
            <el-col :span="6">$S[dashed]</el-col>
          </el-row>
          <el-row :gutter="20">
             <el-col :span="20">$C[].:rltb,:$C[t]</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">$FM[] </el-col>
            <el-col :span="8">$VM[] </el-col>
            <el-col :span="8">:$VA[]</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12"
              >$F[arr1-arr2]arr1arr2</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >()
              $P[,,con.==con.?con.:con.]
              con</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >()
              $PV[,,con.==con.?con.:con.]
              con</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23">
              :$P$PV
              <span style="color: red"></span
              >$PV[,,con.+'>'+con.?con.:con.]
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >$F[arr1-arr2-b-t-l]b,t,l,bt,lr</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="10"></el-col>
            <el-col :span="14" style="color: red"
              >:,</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >$CHECK[] 
              :[{"content":"444",check:"checked"},{"content":"444"}]
              check:"checked"</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >$RADIO[[] 
              :[{"content":"444",select:"checked"},{"content":"444"}]
              select:"checked"</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >$W[400~800]$D[]th400 800</el-col
            >
          </el-row>
          <el-row :gutter="20">
            <el-col :span="23"
              >$TW[400~800],thth</el-col
            >
          </el-row>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div id="editor" style="width: 100%; height: 90%"></div>
  </div>
</template> 
 
<script>
import "../anx-ueditor/ueditor.config.js";
import "../anx-ueditor/ueditor.all.js";
import "../anx-ueditor/lang/zh-cn/zh-cn.js";
export default {
  name: "axueditor",
  data() {
    return {
      editor: null,
    };
  },
  props: {
    toolbars: {
      type: Array,
      default() {
        return [
          "undo", //
          "redo", //
          "inserttable", //
          "mergeright", //
          "mergedown", //
          "deleterow", //
          "deletecol", //
          "splittorows", //
          "splittocols", //
          "splittocells", //
          "deletecaption", //
          "inserttitle", //
          "mergecells", //
          "deletetable", //
          "cleardoc", //
          "fontfamily", //
          "fontsize", //
          "bold", //
          "italic", //
          "justifyleft", //
          "justifyright", //
          "justifycenter", //
          "justifyjustify", //
          "forecolor", //
          "backcolor", //
        ];
      },
    },
    isReData: {
      type: Boolean,
      default() {
        return false;
      },
    },
    isReDataEditor: {
      type: Boolean,
      default() {
        return false;
      },
    },
    dataResultStr: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  mounted() {
    this.editor = UE_COMMON.getEditor("editor", {
      toolbars: [this.toolbars],
      elementPathEnabled: false,
    });
  },
  watch: {
    isReData(val) {
      if (val && isReDataEditor) {
        let dom = this.editor.getContent();
        let dataR = JSON.parse(this.dataResultStr);
        dataR.domStr = dom;
        this.$emit("handleData", dataR);
      }
    },
    isReDataEditor(val) {
      if (!val) {
        let dom = this.editor.getContent();
        this.$emit("handleData", dom);
      }
    },
  },
};
</script>
<style  lang="scss">
</style>



